.team {
    text-align: center;
    grid-area: tm;
}

.date {
    grid-area: dt;
    text-align: right;
}

.venue {
    grid-area: vn;
    text-align: left;
}

.card {
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    grid-auto-rows: minmax(20px, auto);
    grid-template-areas: "tm tm tm tm tm tm tm tm tm" "dt dt dt dt .  vn vn vn vn"
}

* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.card {
    padding: 10px;
    border: 1px solid #ccc;
    margin: 0 0 10px 0;
    text-decoration: none;
    color: black;
}

.card > div {
    border-radius: 5px;
    padding: 1em;
}

h1 {
    padding-bottom: 0.3em;
    margin: 0;
    border: 0;
}



